import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Teams extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadTeams();
    }

	loadTeams() {
		const { teamStore } = this.props.stores;
		teamStore.load();
	}

	render() {
		const { teamStore, userStore } = this.props.stores;
		const { teams } = teamStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h1>Teamene dine - {teams.length} stk</h1>
						<div class='row'>
							{teams && teams.map(team => (
								<div class={`col-6 col-sm-4 col-lg-2 mt-5 p-2 ${user.team === team.id ? 'border border-success rounded' : ''}`}>
									{/* {JSON.stringify(team, null, 4)} */}
									<div class='text-center border rounded p-2 mb-2'>
										<a href={`/teams/${team.id}`}>
											{team.image ? <img src={team.image} class='img-fluid' /> : <i class='fas fa-users text-muted' style='font-size: 7vw;' />}
										</a>
									</div>
									<div>
										{team.name} 
									</div>
									<small>
										<span class='float-right text-muted'>
											{team && team.dogs && team.dogs.length} <i class='fas fa-dog mr-2' />
											{team && team.members && team.members.length} <i class='fas fa-user' />
										</span>
										<i>{team.url}</i><br />
									</small>
								</div>
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/teams/new'
							>
								<i class='fas fa-plus' /> Legg til team
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Teams;
